import React from "react";
import Slider from "react-slick";
import '../common/styles/main.css';


class MainSlider extends React.Component {
  render() {
    console.log(this.props.SliderBg)
    const images = this.props.SliderBg.map((data, index) =>
      <div className="slide" key={index}>
        <img src={"https://dev.winbet-bg.com/uploads/images/sliders/" + data.background_file_name} alt=''/>
        <div className="slider-overlay">
          <h2>{data.front_file_title}</h2>
          <div className="title-white-line"></div>
          <p>{data.front_file_description} 
          </p>
          <a target="_blank" rel="noopener noreferrer" href={data.button_1_bg_url} className={data.button_1_text === '' ? 'slider-register-emty' : 'slider-register'}><span>{data.button_1_text}</span></a>
          <a target="_blank" rel="noopener noreferrer" href={data.button_2_bg_url} className={data.button_2_text === '' ? 'slider-log-in-emty' : 'slider-log-in'}><span>{data.button_2_text}</span></a>
        </div>
      </div>
    );
    var settings = {
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      pauseOnHover: false,
      autoplaySpeed: 8000,
      infinite: true,
      speed: 400,
      fade: true,
      cssEase: 'linear',
      dots: true
    };
    return (
      <div className="main-slider">
        <Slider {...settings} className="full-width-slider">
          {images}
        </Slider>
      </div>
    );
  }
}

export default MainSlider;




